<template>
    <div class="right-content-box re-schedule-reason">
        <div class="search-box">
            <p class="time-condition">
                <span>  </span>
                <el-date-picker
                    v-model="startTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
                &#12288;&#12288;
                <el-date-picker
                    v-model="endTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
                <a class="quick-time-condition" href="javascript:void(0);" :class="{active : quickTime === 'currentWeek'}" @click="quickSelectTime('currentWeek')"></a>
                <a class="quick-time-condition" href="javascript:void(0);" :class="{active : quickTime === 'nextWeek'}" @click="quickSelectTime('previousWeek')"></a>
                <a class="quick-time-condition" href="javascript:void(0);" :class="{active : quickTime === 'currentMonth'}" @click="quickSelectTime('currentMonth')"></a>
                <a class="quick-time-condition" href="javascript:void(0);" :class="{active : quickTime === 'nextMonth'}" @click="quickSelectTime('previousMonth')"></a>
            </p>
            <div class="mt-20 query-conditions" v-show="schedulePersonList.length > 1">
                <span>  </span>
                <el-select v-model="schedulePerson" placeholder="">
                    <el-option
                        v-for="item in schedulePersonList"
                        :key="item.userId"
                        :label="item.userName"
                        :value="item.userId">
                    </el-option>
                </el-select>
            </div>
            <a class="mt-20 default-btn" href="javascript:void(0);" @click="getHistoryList"></a>
        </div>
        <hr>
        <p v-show="!showEChartAndTable" class="c-theme-color">{{showTips}}</p>
        <div>
            <div class="re-schedule-canvas-box mb-40">
                <div id="historyCount" class="canvas"></div>
                <hr style="background-color: #d9d9d9;width: 1px;height: 400px;margin-top: 0;">
                <div id="countAll" class="canvas"></div>
            </div>
            <table class="compare-equipment" v-show="showEChartAndTable">
                <thead>
                <tr>
                    <th style="width: 160px"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="row in historyScheduleTableList">
                    <td>{{row.scheduleTime}}</td>
                    <td>{{row.schemeName}}</td>
                    <td>{{row.location}}</td>
                    <td>{{row.reason}}</td>
                    <td>{{row.detail}}</td>
                    <td><a style="color: #1d7ed8" href="javascript:void(0);" @click="historyScheduleReason(row)"></a></td>
                </tr>
                </tbody>
            </table>
        </div>
        <el-dialog
            title=""
            :visible.sync="historyScheduleReasonShow"
            size="large">
            <reschedule-dialog
              :reasonId="thisRowId"
              :thisScheduleTime="thisRowTime">
            </reschedule-dialog>
        </el-dialog>
    </div>
</template>
<script>

	  import reScheduleDialog from '../common/reScheduleDialog'
    export default {
        components: {
            "reschedule-dialog": reScheduleDialog,
        },
    	  data() {
    	  	  return {
    	  	  	  showTips : '',
                quickTime: "", //
                startTime: new Date().getTime(), //
                endTime: new Date().getTime() + 86400000, //
                schedulePersonList :  [],  //
                schedulePerson : '',  //
                showEChartAndTable : false,  //
                historyScheduleData: {
                    "caclReasonData": {},
                    "caclLocationData": {},
                    "reasonDtoList": []
                },
                historyScheduleTableList : [], //
                showSecond: true,
                dialogTableVisible:false,
                historyScheduleReasonShow:false,
                thisRowId:0,
                thisRowTime:'',
            }
        },
        mounted() {
            this.$nextTick(() => {
                //dom
                this.myChartsHistoryCount = this.echarts.init(document.getElementById('historyCount'));
//                //========
                this.myChartsCountAll = this.echarts.init(document.getElementById('countAll'));
            });
//            //
            this.$http.get(this.url.get_all_schedule_person).then((res) => {
            	  this.schedulePersonList = res.data;
            },(res)=>{
              this.schedulePersonList = []
            });
            this.historyScheduleTableList = this.historyScheduleData.reasonDtoList;
        },
        methods : {
            //
            quickSelectTime(timeName) {
                //class-acitve
                this.quickTime = timeName;
                const year = new Date().getFullYear(),
                    month = new Date().getMonth() + 1,
                    date = new Date().getDate(),
                    weekDay = new Date().getDay() === 0 ? 7 : new Date().getDay(); //,0 
                //true,false,
                if (timeName === "currentWeek") {
                    //
                    this.endTime = new Date();
                    this.startTime = new Date().getTime() - (weekDay - 1) * 86400000;
                    //
                } else if (timeName === "previousWeek") {
                    //
                    this.endTime = new Date().getTime() - weekDay * 86400000;
                    this.startTime = this.endTime - 6 * 86400000;
                }   else if (timeName === "currentMonth") {
                    //
                    this.endTime = new Date();
                    this.startTime = new Date(year + "-" + month + "-01").getTime();
                } else {
                    //
                    this.endTime = new Date(year + "-" + month + "-01").getTime() - 86400000;
                    this.startTime = this.endTime - (getMonthDays(this.endTime) - 1) * 86400000;
                }
            },
            //,
            getHistoryList(){
                //
                if(this.startTime > this.endTime){
                    this.$alert('', '');
                    return;
                }
                const obj = {
                    startTime : getCorrectDate(this.startTime),
                    endTime : getCorrectDate(this.endTime),
                    userId : this.schedulePerson
                };
                this.$http.post(this.url.get_history_list,obj).then((res) => {
                    //
                	  if(!res.data.reasonDtoList || res.data.reasonDtoList.length === 0){
                	  	  this.showTips = ""
                        this.showEChartAndTable = false;//
                        //
                        this.myChartsHistoryCount.clear();
                        this.myChartsCountAll.clear();
                        return;
                    }
                    //
                    this.historyScheduleData = res.data;
                    //
                    this.historyScheduleTableList = this.historyScheduleData.reasonDtoList;
                    this.showEChartAndTable = true;
                    this.packageECharts();
                },(res) => {
                    res.data = {
                        "caclReasonData": {
                          "": 0.5,
                          "": 0.5
                        },
                        "caclLocationData": {
                          "": 0.25,
                          "": 0.25,
                          "": 0.5
                        },
                        "reasonDtoList": [
                        {
                          "id": 5,
                          "scheduleTime": "2017-01-02 19:20:35",
                          "schemeName": "s1",
                          "location": "01 ",
                          "reason": "",
                          "detail": "1"
                        },
                        {
                          "id": 6,
                          "scheduleTime": "2017-01-04 19:20:35",
                          "schemeName": "s1",
                          "location": "01 ",
                          "reason": "",
                          "detail": "2"
                        },
                        {
                          "id": 7,
                          "scheduleTime": "2017-01-04 19:20:35",
                          "schemeName": "s1",
                          "location": "01 ",
                          "reason": "",
                          "detail": "fdsfsdfsdfds"
                        }
                      ]
                    };
                    this.historyScheduleData = res.data;
                    this.historyScheduleTableList = this.historyScheduleData.reasonDtoList;
                    this.showEChartAndTable = true;
                    this.packageECharts();
                });
            },
            packageECharts(){
            	  //,
                if(this.historyScheduleData.reasonDtoList && this.historyScheduleData.reasonDtoList.length){
                    //========
                    const historyTimeCount = this.dataProcess.toHistoryECharts(this.historyScheduleData);
                    const optionData = $.extend({},getBarEChartsOption(historyTimeCount),{interval : 1});//2
                    this.myChartsHistoryCount.setOption(optionData);
                    //,
                    this.myChartsHistoryCount.on("click",(params) => {
                        const time = params.name;
                        this.historyScheduleTableList = this.historyScheduleData.reasonDtoList.filter((item) => {
                              return item.scheduleTime.slice(0,10) === time;
                        })
                    });
                    //============
                    const historyScheduleList = this.dataProcess.getHistorySchedulePieCharts(this.historyScheduleData);
                    this.myChartsCountAll.setOption(getPieEChartsOption(historyScheduleList));
                }
            },
            historyScheduleReason(rowInfo){
                this.thisRowId = rowInfo.id;
                this.thisRowTime = rowInfo.scheduleTime;
                this.historyScheduleReasonShow = !this.historyScheduleReasonShow;
            }
        }
    }
    function getBarEChartsOption(optionData) {
        let option = {
            title : {
                text: optionData.title || '',
            },
            tooltip : {
                trigger: 'axis'
            },
//            legend: {
//                data:[]
//            },
            grid : {
                left: '0%',
                right: '0%',
                bottom: '0%',
                containLabel: true
            },
            xAxis :  {
                type: 'category',
                data: []
            },
            yAxis : {
                type : 'value',
                interval : optionData.interval
            },
            series : []
        };
        option.xAxis.data = optionData.xAxisList;
        optionData.yAxisList.forEach((item) => {
            const obj = {
                name:item.name,
                type: optionData.type || 'bar',
                barWidth :optionData.barWidth,
                data:item.valueList
            };
            option.series.push(obj);
        });
        return option
    }
    function getPieEChartsOption (optionData) {
        let option = {
            title : {
                text: optionData.title || '',
                x:'center'
            },
            tooltip : {
                trigger: 'item',
                formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            animationThreshold : false,//
            legend: {
                x : 'center',
                y : '10%',
            },
            toolbox: {
                show : false,
                feature : {
                    mark : {show: true},
                    dataView : {show: false, readOnly: false},
                    magicType : {
                        show: true,
                        type: ['pie', 'funnel']
                    },
                    restore : {show: true},
                    saveAsImage : {show: true}
                }
            },
            calculable : true,
            series : [
            ]
        };

        const pieNumber = Object.getOwnPropertyNames(optionData).length;
        let count = 1;//
        for(let index in optionData){
            let basePie = {
                type:'pie',
                radius : [0, 80],
                center : ['0%', '50%'],
            };
            basePie.name = optionData[index].name;
            basePie.data = optionData[index].valueList;
            basePie.center[0] = (count / (pieNumber * 2) * 100) + '%';
            option.series.push(basePie);
            count += 2;
        }
        return option;
    }
    /**
     * 2017-07-01
     * @param date
     * @returns 2017-07-01
     */
    function getCorrectDate(date) {
        const currentTime = date ? new Date(date) : new Date();
        //100
        const month = (currentTime.getMonth() + 1) < 10 ? "0" + (currentTime.getMonth() + 1) : (currentTime.getMonth() + 1);
        //100
        const day = currentTime.getDate() < 10 ? "0" + currentTime.getDate() : currentTime.getDate();
        return currentTime.getFullYear() + "-" + month + "-" + day;
    }

    /**
     * desc:
     * time:2017-06-21
     * last : linzx
     * @param: month 
     * @return: 
     **/
    function getMonthDays(date) {
      const curDate = new Date(date || new Date());
      const curMonth = curDate.getMonth();
      curDate.setMonth(curMonth + 1);
      /* 0, , ,  */
      curDate.setDate(0);
      /*  */
      return curDate.getDate();
    }
</script>



